
<template>
  <div>
    <div class="top-style" >
      <div style="display: flex;justify-content: flex-start;">
        <el-input placeholder="..."
                  prefix-icon="el-icon-search"
                  clearable
                  @clear="initEmps"
                  style="width: 250px;margin-right: 10px"
                  v-model="keyword"
                  @keydown.enter.native="initEmps"
                  :disabled="showAdvanceSearchView"></el-input>
        <el-select v-model="searchValue.politicid"
                   style="width: 130px;margin-right: 10px"
                   placeholder="">
          <el-option v-for="item in politicsstatus"
                     :key="item.id"
                     :label="item.name"
                     :value="item.id">
          </el-option>
        </el-select>
        <el-select v-model="searchValue.nationid"
                   placeholder=""
                   style="width: 130px;margin-right: 10px">
          <el-option v-for="item in nations"
                     :key="item.id"
                     :label="item.name"
                     :value="item.id">
          </el-option>
        </el-select>
        <el-select v-model="searchValue.posid"
                   placeholder=""
                   style="width: 130px;margin-right: 10px">
          <el-option v-for="item in positions"
                     :key="item.id"
                     :label="item.name"
                     :value="item.id">
          </el-option>
        </el-select>
        <el-select v-model="searchValue.joblevelid"
                   placeholder=""
                   style="width: 130px;margin-right: 10px">
          <el-option v-for="item in joblevels"
                     :key="item.id"
                     :label="item.name"
                     :value="item.id">
          </el-option>
        </el-select>
        <el-date-picker v-model="searchValue.beginDateScope"
                        type="daterange"
                        unlink-panels
                        value-format="yyyy-MM-dd"
                        range-separator=""
                        start-placeholder=""
                        end-placeholder="">
        </el-date-picker>
        <el-button icon="el-icon-search"
                   type="primary"
                   style="margin-left:10px"
                   @click="initEmps('advanced')"></el-button>
      </div>

      <div style="display: flex;justify-content: flex-end">

      </div>
    </div>

    <div class="content-style">
      <el-scrollbar style="height: 100%">
        <div>
          <el-table :data="emps"
                    stripe
                    border
                    max-height="685"
                    v-loading.fullscreen.lock="loading"
                    element-loading-spinner="fa fa-spinner fa-pulse fa-3x fa-fw"
                    style="width: 100%;height: 695px"
                    @selection-change="handleSelectionChange">
            <el-table-column type="selection"
                             width="55">
            </el-table-column>
            <el-table-column fixed
                             prop="name"
                             align="left"
                             label=""
                             width="100">
            </el-table-column>
            <el-table-column prop="workid"
                             fixed
                             label=""
                             align="left"
                             width="100">
            </el-table-column>

            <el-table-column label="">
              <el-table-column prop="employeetrainList.traincontent"
                               align="left"
                               label="">
              </el-table-column>
              <el-table-column prop="employeetrainList.trainstartdate"
                               align="left"
                               width="110"
                               label="">
              </el-table-column>
              <el-table-column prop="employeetrainList.trainfinishdate"
                               align="left"
                               width="110"
                               label="">
              </el-table-column>
            </el-table-column>

            <el-table-column label="">
              <el-table-column prop="appraise.appcontent"
                               align="left"
                               label="">
              </el-table-column>
              <el-table-column prop="appraise.appdate"
                               align="left"
                               width="110"
                               label="">
              </el-table-column>
            </el-table-column>
            <el-table-column label="">
              <el-table-column width="200"
                               prop="salary.name"
                               label=""></el-table-column>
              <el-table-column width="100"
                               prop="salary.basicsalary"
                               label=""></el-table-column>
            </el-table-column>
            
          

          </el-table>
        </div>
      </el-scrollbar>
    </div>
    <div class="bottom-style">

      <el-pagination style="margin-top: 10px"
                     background
                     @current-change="currentChange"
                     @size-change="sizeChange"
                     layout="sizes, prev, pager, next, jumper, ->, total, slot"
                     :total="total"
                     :page-sizes="[13,33,333,,3333]">
      </el-pagination>

    </div>
    <!---->

  </div>
</template>

<script>
export default {
  name: "EmpAdv",
  data () {
    return {
      multipleSelection: [],
      searchValue: {
        politicid: null,
        nationid: null,
        joblevelid: null,
        posid: null,
        engageform: null,
        departmentid: null,
        beginDateScope: null
      },
      title: '',
      importDataBtnText: '',
      importDataBtnIcon: 'el-icon-upload2',
      importDataDisabled: false,
      showAdvanceSearchView: false,
      allDeps: [],
      emps: [],
      loading: false,
      popVisible: false,
      popVisible1: false,
      dialogVisible: false,
      total: 0,
      page: 1,
      keyword: '',
      size: 13,
      nations: [],
      joblevels: [],
      politicsstatus: [],
      positions: [],
      tiptopDegrees: ['', '', '', '', '', '', '', ''],
      options: [{
        value: '',
        label: ''
      }],
      inputDepName: '',
     
      defaultProps: {
        children: 'children',
        label: 'name'
      },
   
    }
  },
  mounted () {
    this.initEmps();
    this.initData();
    this.initPositions();
  },
  methods: {
   
    
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
    /**/
    searvhViewHandleNodeClick (data) {
      this.inputDepName = data.name;
      this.searchValue.departmentid = data.id;
      this.popVisible1 = !this.popVisible1
    },
    exportData () {
      window.open('/employee/basic/export', '_parent');
    },
   
    showEditEmpView (data) {
      this.initPositions();
      this.title = '';
      this.emp = data;
      this.inputDepName = data.department.name;
      this.dialogVisible = true;
    },
   
   
    handleNodeClick (data) {
      this.inputDepName = data.name;
      this.emp.departmentid = data.id;
      this.popVisible = !this.popVisible
    },
    /**/
    showDepView () {
      this.popVisible1 = !this.popVisible1
    },
    showDepView1 () {
      this.popVisible = !this.popVisible
    },
    /**/
    initPositions () {
      this.getRequest('/employee/basic/positions').then(resp => {
        if (resp) {
          this.positions = resp;
        }
      })
    },
    getMaxWordID () {
      this.getRequest("/employee/basic/maxWorkID").then(resp => {
        if (resp) {
          this.emp.workid = resp.obj;
        }
      })
    },
    /**/
    initData () {
      /**/
      /**/
      if (!window.sessionStorage.getItem("nations")) {
        this.getRequest('/employee/basic/nations').then(resp => {
          if (resp) {
            this.nations = resp;
            window.sessionStorage.setItem("nations", JSON.stringify(resp));
          }
        })
      } else {
        this.nations = JSON.parse(window.sessionStorage.getItem("nations"));
      }
      /**/
      if (!window.sessionStorage.getItem("joblevels")) {
        this.getRequest('/employee/basic/joblevels').then(resp => {
          if (resp) {
            this.joblevels = resp;
            window.sessionStorage.setItem("joblevels", JSON.stringify(resp));
          }
        })
      } else {
        this.joblevels = JSON.parse(window.sessionStorage.getItem("joblevels"));
      }
      /**/
      if (!window.sessionStorage.getItem("politicsstatus")) {
        this.getRequest('/employee/basic/politicsstatus').then(resp => {
          if (resp) {
            this.politicsstatus = resp;
            window.sessionStorage.setItem("politicsstatus", JSON.stringify(resp));
          }
        })
      } else {
        this.politicsstatus = JSON.parse(window.sessionStorage.getItem("politicsstatus"));
      }
      /**/
      if (!window.sessionStorage.getItem("deps")) {
        this.getRequest('/employee/basic/deps').then(resp => {
          if (resp) {
            this.allDeps = resp;
            window.sessionStorage.setItem("deps", JSON.stringify(resp));
          }
        })
      } else {
        this.allDeps = JSON.parse(window.sessionStorage.getItem("deps"));
      }
    },
    /**/
    sizeChange (currentSize) {
      this.size = currentSize;
      this.initEmps();
    },
    currentChange (currentPage) {
      this.page = currentPage;
      this.initEmps();
    },
    showAddEmpView () {
      this.emptyEmp();
      this.title = '';
      this.getMaxWordID();
      this.dialogVisible = true;
    },
    /**/
    initEmps () {
      this.loading = true;
      let url = '/employee/advanced/?page=' + this.page + '&size=' + this.size;
  if (this.searchValue != null) {
     
      }
      if (this.searchValue.politicid) {
        url += '&politicid=' + this.searchValue.politicid;
      }
      if (this.searchValue.nationid) {
        url += '&nationid=' + this.searchValue.nationid;
      }
      if (this.searchValue.joblevelid) {
        url += '&joblevelid=' + this.searchValue.joblevelid;
      }
      if (this.searchValue.posid) {
        url += '&posid=' + this.searchValue.posid;
      }
      if (this.searchValue.engageform) {
        url += '&engageform=' + this.searchValue.engageform;
      }
      if (this.searchValue.departmentid) {
        url += '&departmentid=' + this.searchValue.departmentid;
      }
      if (this.searchValue.beginDateScope) {
        url += '&beginDateScope=' + this.searchValue.beginDateScope;
      }
      if (this.keyword) {
        this.$notify.success({
        title: '',
        message: '    ...',
        showClose: false,
        offset: 100,
        duration: 1500,
        customClass: 'fontclass'
      });
        url += "&name=" + this.keyword;
      }
      /* */
       this.$notify.success({
                        title: '',
                        message: '      ...',
                        showClose: false,
                        offset: 100,
                        duration: 1500,
                        customClass: 'fontclass'
                    });
      this.getRequest(url).then(resp => {
        this.loading = false;
        if (resp) {
          this.emps = resp.data;
          /**/
          this.total = resp.total;
          this.searchValue = [];
          this.keyword = '';
        }
      });
    }
  }
}
</script>

<style>
.top-style {
  /**/
  display: flex;
  /**/
  justify-content: space-between;
}

.content-style {
  margin-top: 10px;
}

.content-style .el-scrollbar__wrap {
  overflow: scroll;
}

.bottom-style {
  display: flex;
  justify-content: space-between;
}

.fontclasssys {
  font-family: ;
}

/*  */
/*  */
.slide-fade-enter-active {
  transition: all 0.8s ease;
}

.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter, .slide-fade-leave-to
        /* .slide-fade-leave-active for below version 2.1.8 */
 {
  transform: translateX(10px);
  opacity: 0;
}
</style>