<template>
  <div>
    <el-calendar :first-day-of-week="7">
      <template slot="dateCell" slot-scope="{ data }" >
        {{ data.day.split("-").slice(2).join("-") }}
        <br>
        <el-button type="text" @click="open(data)"  v-if="allDate.indexOf(data.day) === -1"></el-button>
        <div v-if="allDate.indexOf(data.day) !== -1  " @click="handleDateChange" style="color: red">
          
        </div>
      </template>
    </el-calendar>


  </div>

</template>
<style>
.is-selected {
  color: #1989FA;
}
</style>
<script>
export default {
  data() {
    return {
      wordDate:{
        date:""
      },
      selectedDate: new Date(), // 
      allDate: [],
      calendarData:[ { day: "2023-03-15",status:"pass", },
        { day: "2021-04-06",status:"unpass", },]
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    isDateGreaterThanToday(date) {
      const today = new Date();
      const select = new Date(date.day);
      return select.getTime() > today.getTime();
    },
    handleDateChange(date) {
      this.$notify.success({
        title: '',
        message: '',
        showClose: false,
        offset: 100,
        duration: 1500,
        customClass: 'fontclass'
      });
    },
    init(){
      this.getRequest("/system/hr/base").then(resp => {
        if (resp) {
          this.allDate = resp[0].workDate;
        }

      });
    },

    open(data) {
      if (this.isDateGreaterThanToday(data)){
        this.$notify.error({
          title: '',
          message: '! ',
          showClose: false,
          offset: 100,
          duration: 1500,
          customClass: 'fontclass'
        });
        return;
      }
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        console.log(data.day)
        this.wordDate.date = data.day;
        this.postRequest("/system/hr/wordDate", this.wordDate).then(resp => {
          if (resp) {
             this.init();
          }

        });
        this.$notify.success({
          title: '',
          message: '',
          showClose: false,
          offset: 100,
          duration: 1500,
          customClass: 'fontclass'
        });
      }).catch(() => {
        this.$notify.warning({
          title: '',
          message: '',
          showClose: false,
          offset: 100,
          duration: 1500,
          customClass: 'fontclass'
        });
      });
    }
  },
};
</script>
